import React from "react"

import { AuthProvider } from "../hooks/auth"
import Layout from "../components/Layout"
import Banner from "../components/utils/Banner"

import AboutUs from "../components/pages/AboutUs"
import SEO from "../components/utils/SEO"

import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

const About = () => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "about-us",
			name: translate("about.breadCrumb.name"),
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Quem Somos"
				description="A Kivalita Consulting é uma consultoria única que busca soluções rápidas, atendendo aos mais rigorosos requisitos regulamentares e de mercado da qualidade."
				image="/images/about-us.jpeg"
			></SEO>
			<AuthProvider>
				<Banner titlePage={translate("banner.about")} />
				<AboutUs />
			</AuthProvider>
		</Layout>
	)
}

export default About
