import React, { useState } from "react"

import { Container, Description } from "./styles"

import Button from "../../../../utils/Button"
import { translate } from "../../../../utils/translate"
import useScript from "../../../../../hooks/external-scripts"
import { Modal } from "react-responsive-modal"

export default function sectFour() {
	const [showModal, setShowModal] = useState(false)
	const modalUrl =
		"new RDStationForms('portifolio-dcfa720067e2e39fcee4', 'UA-156947544-1').createForm()"

	useScript(
		"https://d335luupugsy2.cloudfront.net/js/rdstation-forms/stable/rdstation-forms.min.js",
		true
	)

	const openModal = () => {
		setShowModal(true)
		setTimeout(() => {
			const script = document.createElement("script")
			script.append(modalUrl)
			document.body.appendChild(script)
		}, 1000)
	}
	const closeModal = () => setShowModal(false)

	return (
		<div className="container">
			<section id="our-employees">
				{showModal && (
					<Modal open={showModal} onClose={closeModal} center>
						<div role="main" id="portifolio-dcfa720067e2e39fcee4"></div>
					</Modal>
				)}
				<div className="row">
					<div className="col-sm">
						<Description>
							<p>{translate("about.four.title")}</p>
							<h3>{translate("about.four.subTitle")}</h3>
							<hr />
						</Description>
					</div>
				</div>
				<div className="row">
					<div className="col-sm">
						<Container>
							<a
								href="https://www.kivalitaconsulting.com/wp-content/uploads/2019/09/English-Version-2019-Kivalita-Consulting.pdf"
								target="_blank"
							>
								<Button
									title={translate("about.four.button1")}
									bgColor={"#f4bf8c"}
									fontFamily={"Poppins"}
									fontSize={17}
									fontWeight={600}
									textColor={"#FFF"}
									padding={"18px 40px 18px 40px"}
								/>
							</a>
							<div
								onClick={() => {
									openModal()
								}}
							>
								<Button
									title={translate("about.four.button2")}
									bgColor={"#f4bf8c"}
									fontFamily={"Poppins"}
									fontSize={17}
									fontWeight={600}
									textColor={"#FFF"}
									padding={"18px 40px 18px 40px"}
								/>
							</div>
						</Container>
					</div>
				</div>
			</section>
		</div>
	)
}
