import styled from "styled-components"

export const Container = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	max-width: 1400px;
	margin: 0 auto;

	@media (max-width: 576px) {
		display: none;
	}
`

export const SubContainer = styled.div`
	position: relative;
	top: -40px;

	img {
		width: calc(33% - 16px);
		border-radius: 8px;
		margin: 0px 8px 0px 8px;
	}
`
