import React from "react"

import { FaChevronRight } from "react-icons/fa"

import { Container, Description, ButtonRedir } from "./styles"

import Button from "../../../../utils/Button"

import Thumb from "../../../../../assets/aboutus/thumb.jpg"
import { translate } from "../../../../utils/translate"

export default function sectTwo() {
	return (
		<div className="container">
			<section id="about-us">
				<Container>
					<div className="row">
						<div className="col-sm-12 col-md-5 col-lg-6">
							<iframe
								max-width="560"
								max-height="315"
								width="100%"
								height="100%"
								src={translate("about.two.video")}
								title="YouTube video player"
								frameborder="0"
								allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
								allowfullscreen
							></iframe>
						</div>
						<div className="col-sm-12 col-md-7 col-lg-6">
							<Description>
								<h2>{translate("about.two.part1")}</h2>
								<h2>{translate("about.two.part3")}</h2>
							</Description>
						</div>
					</div>
				</Container>
			</section>
		</div>
	)
}
