import styled from "styled-components"

export const Container = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	margin-bottom: 56px;

	iframe {
		width: 100%;
		height: 280px;
		margin-top: 16px;
	}

	img {
		width: 100%;
		max-height: 280px;
		margin-top: 16px;
		object-fit: cover;
	}
`

export const Description = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

	margin-top: 70px;
	letter-spacing: 7px;
	font-size: 12px;
	font-weight: 300;
	font-family: "Montserrat", sans-serif;
	color: #444;

	h3 {
		font-size: 35px;
		font-weight: 600;
		letter-spacing: 0ch;
		color: #444;
	}

	hr {
		border: 1px solid #f4bf8c;
		width: 50px;
		margin: 15px 0px 30px 0px;
	}
`
