import React, { useState, useEffect, useCallback } from "react"
import Swiper from "react-id-swiper"
import "swiper/css/swiper.css"

import ImageService from "../../../../../services/image-service"
import { IImage } from "../../../../../interfaces"
import convertImageToBase64 from "../../../../utils/convertImageToBase64"
import UserLogged from "../../../../utils/UserLogged"
import { useAuth } from "../../../../../hooks/auth"

import {
	Description,
	ButtonUpload,
	ButtonAlter,
	ModalTitle,
	Button,
} from "./styles"

import { translate } from "../../../../utils/translate"
import { Modal } from "react-responsive-modal"

export default function sectThree() {
	const imageService = new ImageService()
	const { user } = useAuth()
	const [images, setImages] = useState<IImage[]>([])
	const [selectedImage, setSelectesImage] = useState<IImage>(null)
	const [showModal, setShowModal] = useState(false)
	const [imageToDelete, setImageToDelete] = useState(0)

	console.log(user)
	const [paramsSwiper, setParamsSwiper] = useState<object>({
		effect: "coverflow",
		grabCursor: false,
		centeredSlides: true,
		slidesPerView: 3,
		navigation: {
			nextEl: ".swiper-button-next",
			prevEl: ".swiper-button-prev",
		},
	})

	const setSlidesPerView = () => {
		if (window != undefined) {
			if (window.innerWidth < 992) {
				setParamsSwiper(Object.assign(paramsSwiper, { slidesPerView: 1 }))
			} else {
				setParamsSwiper(Object.assign(paramsSwiper, { slidesPerView: 3 }))
			}
		}
	}

	useEffect(() => {
		setSlidesPerView()

		imageService.list<IImage[]>("About").then(imageList => {
			setImages(imageList)
		})
	}, [])

	const insertImage = useCallback(async (event: any) => {
		const file = event.target.files[0]

		convertImageToBase64(file).then(async base64 => {
			await imageService.create({
				imageString: base64,
				type: "About",
				userId: user.id,
			})
		})
	}, [])

	const updateImage = async (event: any) => {
		const file = event.target.files[0]
		convertImageToBase64(file).then(async base64 => {
			selectedImage.imageString = base64
			await imageService.update(selectedImage)
		})
	}

	const deleteImage = async (imageId: number) => {
		await imageService.delete(imageId)
		const newImages = images.filter(image => image.id !== imageId)
		setImages(newImages)
		setShowModal(false)
	}

	return (
		<div className="container" style={{ marginTop: "48px" }}>
			{showModal && (
				<Modal open={showModal} onClose={() => setShowModal(false)} center>
					<ModalTitle>Tem certeza que deseja deletar esta imagem?</ModalTitle>
					<Button
						onClick={async () => {
							await deleteImage(imageToDelete)
						}}
					>
						Tenho certeza
					</Button>
				</Modal>
			)}
			<section id="our-work">
				<div className="row">
					<div className="col-sm">
						<Description>
							{translate("about.three.title")}
							<h3>{translate("about.three.subTitle")}</h3>
							<hr />
							<p>{translate("about.three.description")}</p>
						</Description>
					</div>
				</div>
				<div className="row">
					<div className="col-sm">
						<div style={{ marginBottom: "32px" }}>
							<UserLogged>
								<ButtonUpload htmlFor="file">Adicionar Imagem</ButtonUpload>
								<input
									type="file"
									name="file"
									id="file"
									hidden
									onChange={insertImage}
								/>
							</UserLogged>
							{images.length > 0 && (
								<Swiper {...paramsSwiper}>
									{images.map(inImage => (
										<div
											key={inImage.fileName}
											// href={image.imageUrl}
											data-lightbox="about"
										>
											<div
												style={{
													height: "400px",
													background: "no-repeat 50%",
													backgroundSize: "contain",
													backgroundImage: `url(${inImage.imageUrl})`,
												}}
											/>
											<div className="row">
												<div className="col-6">
													<UserLogged>
														<ButtonUpload
															htmlFor="NewImage"
															onClick={() => {
																setSelectesImage(inImage)
															}}
														>
															Alterar Imagem
														</ButtonUpload>
														<input
															type="file"
															name="NewImage"
															id="NewImage"
															hidden
															onChange={event => updateImage(event)}
														/>
													</UserLogged>
												</div>
												<div className="col-6">
													<UserLogged>
														<ButtonUpload
															style={{
																borderColor: "#fc8383",
																color: "#fc8383",
															}}
															onClick={() => {
																setImageToDelete(inImage.id)
																setShowModal(true)
															}}
														>
															Deletar Imagem
														</ButtonUpload>
													</UserLogged>
												</div>
											</div>
										</div>
									))}
								</Swiper>
							)}
						</div>
					</div>
				</div>
			</section>
		</div>
	)
}
