import styled from "styled-components"

export const Container = styled.div`
	margin-top: 32px;

	img {
		width: 100%;
		padding-right: 24px;
	}

	@media (max-width: 768px) {
		img {
			padding-right: 0;
		}
	}
`

export const ButtonRedir = styled.div`
	position: relative;
	bottom: 24px;
	float: right;

	@media (max-width: 768px) {
		margin: 16px auto;
		bottom: 0;
		float: none;
		text-align: center;
	}
`

export const Description = styled.div`
	margin-top: auto;
	margin-bottom: auto;
	h2 {
		color: #444;
		font-family: "Montserrat", sans-serif;
		font-size: 20px;
		font-weight: 300;
		margin-bottom: 20px;
	}
	li {
		list-style-type: none;
		font-family: "Montserrat", sans-serif;
		font-size: 17px;
		font-weight: 300;
		margin-bottom: 8px;

		span {
			color: #115964;
			margin-left: 8px;
		}

		svg {
			color: #115964;
		}
	}

	@media (max-width: 576px) {
		margin-top: 16px;
	}
`
